<script>
import qs from "qs";

export default {
  data() {
    return {
      list: {}, //
      emptyRules: [(v) => !!v || ""],
      newData: false, //,
    };
  },

  mounted() {
    this.getAllData();
  },
  methods: {
    //list
    createList() {
      var list = {};
      var colNameArr = [
        { num: 8, name: "identity" },
        { num: 8, name: "education" },
        { num: 7, name: "occupation" },
        { num: 29, name: "diet" },
        { num: 12, name: "sleep" },
        { num: 38, name: "excretion" },
        { num: 13, name: "action" },
        { num: 12, name: "fall" },
        { num: 10, name: "smokewine" },
        { num: 6, name: "leisure" },
        { num: 4, name: "appearance" },
        { num: 8, name: "emotionalstate" },
        { num: 7, name: "occupation" },
        { num: 9, name: "skincondition" },
        { num: 11, name: "sensoryperception" },
        { num: 9, name: "respiratorysystem" },
        { num: 6, name: "sputum" },
        { num: 7, name: "cardiovascular" },
        { num: 16, name: "gastrointestinal" },
        { num: 12, name: "urinary" },
        { num: 6, name: "reproductive" },
        { num: 6, name: "skeletalmuscle" },
      ];
      colNameArr.forEach((item) => {
        for (var i = 1; i <= item.num; i++) {
          list[item.name + i] = "";
        }
        this.list = list;
      });
    },
    getAllData() {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey,
      };

      //
      //this.$store.commit("setLoadingOn"); //on
      this.axios
        .post(
          "general/getByKey/" +
            this.$store.state.databaseName +
            "/nursingassessment",
          qs.stringify(postData)
        )
        .then((rs) => {
          if (rs.data.length == 0) {
            //
            this.newData = true;
            // this.createList();
            // this.addNew();
          } else {
            this.newData = false;
            var matchData = rs.data.map((i) => {
              
              if (i.datalist != "") {
                var newItem = JSON.parse(i.datalist);
                newItem.snkey = i.snkey;
                return newItem;
              }
            });
            this.list = matchData[0];
          }
          // this.$store.commit("setLoadingOff"); //off
        });
    },
    addNew() {
      //
      this.$store.commit("getTime"); //

      var addData = {
        u_snkey: this.$store.state.uData.snkey,
        datalist: JSON.stringify(this.list),
        create_man:
          this.$store.state.pData.username + "(" + this.$store.state.time + ")", //,,
      };

      this.axios
        .post(
          "general/add/" +
            this.$store.state.databaseName +
            "/nursingassessment",
          qs.stringify(addData)
        )
        .then((rs) => {
          if (rs.data.state == 1) {
            var pop = { msg: "", type: true, theme: "success" };
            this.$store.commit("snackbar", pop);
            this.newData = false;
            this.getAllData();
          }
        });
    },
    editOK() {
      //
      if (this.$refs.form.validate()) {
        if (this.newData) {
          console.log('run add');
          this.addNew();
        } else {
          console.log('run edit');
          this.$store.commit("getTime"); //
          var postData = {
            snkey: this.list.snkey,
            datalist: JSON.stringify(this.list),
            edit_man:
              this.$store.state.pData.username +
              "(" +
              this.$store.state.time +
              ")", //,,
          };



          this.axios
            .post(
              "general/edit/" +
                this.$store.state.databaseName +
                "/nursingassessment",
              qs.stringify(postData)
            )
            .then((rs) => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
              }
            });
        }
      } else {
        var pop = {
          msg: "",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
      }
    },
  },
};
</script>


